const Book = ({ id, imgUrl, title, author, eventListener, number }) => {
  const handleClick = () => {
    const book = eventListener(id);
    console.log(book);
  };

  return (
    <article key={id} className="book" onClick={handleClick}>
      <img src={imgUrl} alt={title} />
      <h2>{title}</h2>
      <h2>{author}</h2>
      <span className="number">{`#${number + 1}`}</span>
    </article>
  );
};

export default Book;
