import React, { useState, useRef } from "react";
import AuthorInput from "./input/AuthorInput";
import ImgUrlInput from "./input/ImgUrlInput";
import TitleInput from "./input/TitleInput";

let id = 5;

const FormModal = ({ addBook }) => {
  const [author, setAuthor] = useState("");
  const [title, setTitle] = useState("");
  const [imgUrl, setImgUrl] = useState("");
  const formRef = useRef(null);
  const overlayRef = useRef(null);
  const buttonRef = useRef(null);

  const formEvent = (e) => {
    e.preventDefault();

    const newBook = { id, imgUrl, title, author };
    addBook(newBook);

    // Reset the form fields
    setAuthor("");
    setTitle("");
    setImgUrl("");

    id++;
    closeModal();
  };

  const openModal = () => {
    formRef.current.classList.add("active");
    overlayRef.current.classList.add("active");
    buttonRef.current.classList.add("hide");
  };

  const closeModal = () => {
    formRef.current.classList.remove("active");
    overlayRef.current.classList.remove("active");
    buttonRef.current.classList.remove("hide");
  };

  const handleAuthorChange = (e) => {
    setAuthor(e.target.value);
  };

  const handleTitleChange = (e) => {
    setTitle(e.target.value);
  };

  const handleImgUrlChange = (e) => {
    setImgUrl(e.target.value);
  };

  return (
    <section>
      <form action="" className="modal" ref={formRef} onSubmit={formEvent}>
        <div>
          <AuthorInput
            author={author}
            handleAuthorChange={handleAuthorChange}
          />
          <TitleInput title={title} handleTitleChange={handleTitleChange} />
          <ImgUrlInput
            imgUrl={imgUrl}
            handleImgUrlChange={handleImgUrlChange}
          />
        </div>
        <button type="submit">Add Book</button>
      </form>
      <div className="overlay" onClick={closeModal} ref={overlayRef}></div>
      <button ref={buttonRef} onClick={openModal} className="add-book">
        Add Book
      </button>
    </section>
  );
};

export default FormModal;
