const ImgUrlInput = ({ imgUrl, handleImgUrlChange }) => {
  return (
    <div>
      <label htmlFor="image-link">Image Link:</label>
      <input
        type="url"
        name="image-link"
        id="image-link"
        value={imgUrl}
        onChange={handleImgUrlChange}
      />
    </div>
  );
};

export default ImgUrlInput;
